import { JsonConvertableImpl, BpmnModelJsonHelper } from "@edp-aif/common-api";

export class TableSchema extends JsonConvertableImpl {
  code: string;
  maxDisplayNumber: number = 2000;
  isMainTable: boolean = false;
  isDisplay: boolean = true;

  protected ResetBeforeLoadFrom(): void {
    super.ResetBeforeLoadFrom();

    this.code = null;
    this.maxDisplayNumber = 2000;
    this.isMainTable = false;
    this.isDisplay = true;
  }

  public ConvertToJsonObject(): {} {
    let obj: {} = super.ConvertToJsonObject();

    BpmnModelJsonHelper.SetAttributeValue(obj, "code", this.code);
    BpmnModelJsonHelper.SetAttributeValue(obj, "maxDisplayNumber", this.maxDisplayNumber);
    BpmnModelJsonHelper.SetAttributeValue(obj, "isMainTable", this.isMainTable);
    BpmnModelJsonHelper.SetAttributeValue(obj, "isDisplay", this.isDisplay);

    return obj;
  }

  public LoadFromJsonObject(obj: {}): void {
    super.LoadFromJsonObject(obj);

    this.code = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "code");
    this.maxDisplayNumber = BpmnModelJsonHelper.GetAttributeValueAsNumber(obj, "maxDisplayNumber");
    this.isMainTable = BpmnModelJsonHelper.GetAttributeValueAsBoolean(obj, "isMainTable");
    this.isDisplay = BpmnModelJsonHelper.GetAttributeValueAsBoolean(obj, "isDisplay");
  }
}
